export default {
	data() {
		return {
			goodssData : [],
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			freight:0,			//运费
			sumPrice:0,			//实付款
			deduction:34,		//优惠金额
			addPrice:0, 	//商品总金额
			couponList: [
				{
					title: '新用户专享优惠券',
					price: 5,
				},
				{
					title: '庆五一发一波优惠券',
					price: 10,
				},
				{
					title: '优惠券优惠券优惠券优惠券',
					price: 15,
				}
			],
			addressData: {
				name: '许小星',
				mobile: '13853989563',
				addressName: '金九大道',
				address: '山东省济南市历城区',
				area: '149号',
				default: false,
			},
		}
	},
	onLoad(option){
		//商品数据
		console.log(option.id);
		uni.getStorage({
			key:'selectAddress',
			success: (e) => {
				this.recinfo = e.data;
				uni.removeStorage({
					key:'selectAddress'
				})
			}
		})
	},
	onShow(){
		this.addgoods();
	},
	methods: {
		//显示优惠券面板
		toggleMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(()=>{
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type){
			this.payType = type;
		},
		submit(){
			uni.redirectTo({
				url: '/pages/shop/money/pay'
			})
		},
		stopPrevent(){
			console.log("AAA");
		},
		addgoods(){
			uni.getStorage({
				key:'productGoods',
				success: (ret) => {
					this.goodssData = ret.data;
					console.log(this.goodssData);
					for(let i=0;i<this.goodssData.length;i++){
						this.addPrice = this.addPrice + (this.goodssData[i].number*this.goodssData[i].price);
						console.log(this.addPrice);
						console.log(this.goodssData[i].number);
						console.log(this.goodssData[i].price);
						
					}
					this.sumPrice = this.addPrice-this.deduction+this.freight;
				}
			});
		}
	}
}
